<template>
  <article class="">
    <h3>Editar Comercio</h3>
    <section class="sale_modal" style="margin: 20px 0">
      <label ><b>QR</b></label>
      <div class="sale_modal__content" style="padding-bottom: 30px;">
        <vue-qrcode :value="$route.params.id.toString()" scale="10" />
      </div>
    </section>
    <section >
      <label for=""><b>Nombre</b></label>
      <v-text-field
        v-model="commerce.name"
        label="Nombre"
        :rules="[rules.required]"
      ></v-text-field>
      <label for=""><b>Descripción</b></label>
      <v-text-field
        v-model="commerce.description"
        label="Descripción"
        multi-line
        :rules="[rules.required]"
      ></v-text-field>


      <label for=""><b>Categorias</b></label>
      <v-flex xs12>
        <v-select
          :items="categories"
          v-model="selected_categories"
          label="Seleccione las categorias"
          multiple
          max-height="400"
          persistent-hint
          item-text="name"
          item-value="id"
        ></v-select>
      </v-flex>


      <label for=""><b>Imagen</b></label>
      <div class="trainers__form--photo_container">
        <label
          for="commercePhoto"
          style="
          width: 160px;
          height: 34px;"
          class="trainers__form--photo_button">Examinar</label>
        <input
          id="commercePhoto"
          type="file"
          @change="onFileChange"
          name="logo">
        <p v-if="!url_change">{{ photo_name }}</p>
      </div>
      <v-flex xs12 style="margin: 25px 0; text-align: center;">
        <img :src="url" alt="" width="400px" style="width: 400px; min-height: 200px;"/>
      </v-flex>

      <label for=""><b>Usuario administrador</b></label>
      <v-text-field
        v-model="admin_user.email"
        label="Email"
        :rules="[rules.required, rules.email]"
      ></v-text-field>
      <label for=""><b>Contraseña</b></label>
      <v-text-field
        v-model="admin_user.password"
        label="Contraseña"
        :rules="[rules.required]"
      ></v-text-field>

      <v-switch
        v-if="this.getSuperAdmin()"
        :label="`Publicar: ${commerce.published ? 'Si' : 'No'}`"
        v-model="commerce.published"
      ></v-switch>
      <v-btn
        @click="openModal = true">Editar</v-btn>
      <v-btn @click="$router.push({name: 'comercios'})">Cancelar</v-btn>
    </section>
    <section
      v-if="openModal"
      class="validate_modal">
      <section
        class="validate_modal__content">
        <p>¿Esta seguro que desea realizar los cambios?</p>
        <div class="validate_modal__buttons">
          <v-btn @click="editCommerce()">Si</v-btn>
          <v-btn @click="openModal = false">No</v-btn>
        </div>
      </section>
    </section>
  </article>
</template>

<script>
export default {
  data(){
    return {
      openModal:false,
      qr: true,
      show_qr_modal: false,
      photo_name: 'No se han seleccionado archivos',
      url_change: false,
      url:'https://twenti.s3.us-west-2.amazonaws.com/default_image.svg',
      commerces:[],
      commerce_selected: null,
      categories:[],
      selected_categories:[],
      commerce:{
        id:'',
        name: '',
        description: '',
        image: null,
        published:false
      },
      admin_user:{
        email:"",
        password:""
      },
      rules: {
        required: value => !!value || 'Campo requerido.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'E-mail invalido.'
        },
      }
    }
  },
  methods:{
    findCategories(){
      try {
        this.$http.get('categories',
        ).then(function(response){
          console.log(response);
          this.categories = response.body.data
          console.log("Congrats");
        },function(response){
          console.log("Error");
          console.log(response);
        })
      } catch (e) {
        console.log("Error");
        console.log(e);
      }
    },
    findCommerces(){
      try {
        this.$http.get('commerces/'+this.$route.params.id,
        ).then(function(response){
          console.log(response);
          this.commerce = response.body.data.attributes;
          this.selected_categories = this.commerce.category_ids
          this.url = this.getServer() + this.commerce.image.url
          this.admin_user.email = this.commerce.admin_user ? this.commerce.admin_user.email : ''
          console.log("Congrats");
        },function(response){
          console.log("Error");
          console.log(response);
        })
      } catch (e) {
        console.log("Error");
        console.log(e);
      }
    },
    validateCommerce(){
      if(this.commerce.name != '' &&
         this.commerce.description != '' &&
         this.commerce.image != null){
        return true
      } else {
        return false
      }
    },
    validateUser(){
      if(this.admin_user.email != '' &&
         this.admin_user.password != ''){
        return true
      } else {
        return false
      }
    },
    editCommerce(){
      if(this.validateCommerce()){
        try {
          this.$http.put('commerces/'+this.$route.params.id, {
            data:{
              attributes: this.commerce,
              category_ids: this.selected_categories,
              user: this.validateUser() ? this.admin_user : null
            }
          }
          ).then(function(response){
            console.log("Update");
            console.log(response);
            this.openModal = false
            this.$router.push({name: 'comercios'})
          },function(response){
            console.log("Error");
            console.log(response);
            this.openModal = false
          })
        } catch (e) {
          console.log("Error");
          console.log(e);
          this.openModal = false
        }
      } else {
        alert('Por favor, verifique los valores indexados')
      }
    },
    onFileChange (e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) {
        return
      }
      this.url = URL.createObjectURL(files[0]);
      this.createImage(files[0])
    },
    createImage (file, name_field) {
      var reader = new FileReader()
      var vm = this
      reader.readAsDataURL(file);
      reader.onload = function () {
        vm.commerce.image = reader.result
      }
    },
    showQR(){
      var vm = this;
      this.show_qr_modal = true
      setTimeout(function(){
        var qrcode = new QRCode(document.getElementById("qrvalidate"));
        qrcode.makeCode(vm.$route.params.id.toString());
        vm.qr = !vm.qr;
        $("canvas").width("90%");
        $("canvas").css({"margin-left":"auto", "margin-right": "auto"});
        $("#qrvalidate img").css({"margin-left":"auto", "margin-right": "auto"});
      }, 500);
      // qrcode.makeCode('{"data":{"id_user":'+this.getUserId()+',"id_offer":'+this.offer.id+'}}');
    },
  },
  watch:{
    url(){
      this.url_change = true;
    },
  },
  mounted(){
    this.findCategories()
    this.findCommerces()
  }
}
</script>
<style>
  .validate_modal{
    position: fixed;
    z-index: 99;
    background-color: rgba(0,0,0,0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .validate_modal__content{
    background-color: #fff;
    border-radius: 5px;
    width: 300px;
    padding: 25px;
    text-align:center;
  }
  .validate_modal__buttons{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>



